import { Component, Input, OnInit, Output , EventEmitter   } from '@angular/core';
import { AbstractControl, FormBuilder, FormControl, FormGroup, ValidatorFn, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { AccountService } from '../_service/account.service';


@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  //models: any = {};
  @Output() canceRegister = new EventEmitter<boolean>();
  registerForm: FormGroup;
  // nhn li tr v t Interceptor
  // nu khng khi to i tng s  nhn li Undefiend do c kim tra validationErrors.length > 0
  // khong co doi tuong lam sao co do dai lengthh
  validationErrors: string[] = []; 
  maxDate: Date;
  constructor(
    private accountService: AccountService , 
    private toastr: ToastrService,
    private fb: FormBuilder,
    private router: Router 
    ) { }

  ngOnInit(): void {
  this.intitializeForm();
  this.maxDate = new Date();
  this.maxDate.setFullYear(this.maxDate.getFullYear() - 18);
  }
  
  register() {
   this.accountService.register(this.registerForm.value).subscribe((user) => {
    // console.log(user);
    //this.cance();
    this.router.navigateByUrl("/members");
   } , error => {
     this.validationErrors = error; // Interceptor s tr v Li nu n xy ra
      // console.log(error);
      // this.toastr.error(error.error);
    });
  }
  cance() {
    this.canceRegister.emit(false);
  }
  intitializeForm() {
    this.registerForm = this.fb.group({
      gender:["male"],
      username:["" , Validators.required],
      knownAs:["" , Validators.required],
      dateOfBirth:["" , Validators.required],
      city:["" , Validators.required],
      country:["" , Validators.required],
      password: ['' , [Validators.required , Validators.minLength(4) , Validators.maxLength(8)]],
      confirmPassword: ['' , [Validators.required , this.matchValues('password')]]
     });
  }
  matchValues(matchTo: string) : ValidatorFn {
    return (control: AbstractControl) => {
      return control?.value == control?.parent?.controls[matchTo].value? null : {isMatching: true}
    }
  }
}
