import { User } from "./user";

export class UserParams {
    gender: string;
    minAge:number =  18;
    maxAge: number = 99;
    pageNumber: number = 1;
    pageSize: number =  2;
    orderBy:string = "lastActive" ;
    constructor(user:  User) {
        // gia tri mac dinh
        this.gender = user.gender === 'female' ? 'male' : 'female';
    }
}
