import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { HubConnection, HubConnectionBuilder } from '@microsoft/signalr';
import { ToastrService } from 'ngx-toastr';
import { BehaviorSubject } from 'rxjs';
import { take } from 'rxjs/operators';
import { environment } from 'src/environments/environment';
import { User } from '../_models/user';

@Injectable({
  providedIn: 'root'
})
// kim tr mt ngi l Online hay khng
export class PresenceService { 
  hubUrl = environment.hubUrl;
  hubConnection: HubConnection; // goi trong Signalr
  // new BehaviorSubject<string[]>([]);  cha Mng string nhng ngi ang online kh to l Rng
  private userOnlineSource = new BehaviorSubject<string[]>([]);
  // ly d liu UserOnlineSource khi n next
  onlineUser$ = this.userOnlineSource.asObservable();

  constructor(private toast: ToastrService , private router: Router) { }
  createHubConnection(user: User) {
    // chng ta ko th s dng Token trong Header 
    // do  phi to HubConnectionBuilder  s dng

    this.hubConnection = new HubConnectionBuilder()
    // a ch API ca this.hubUrl + "presence" c 
    // nh ngha trong Startup.cs endpoints.MapHub<PresenceHub>("hubs/presence");
                          .withUrl(this.hubUrl + "presence" , {
    // s dng token ca user curent truyn nh l QueryString cho Hub API
    // //cu hinh IdentityServiceExtension cho php Client send Token
                            accessTokenFactory: () => user.token 
                          })
                          // khi c vn  v mng , s t ng kt ni li cho Hub
                          .withAutomaticReconnect()
                          .build()
    
    //Start hubConnection khi Login v Register
    this.hubConnection.start().catch(error => console.log(error));
    
    // listen "UseOnlineMethod"
    //// username l danh sch nhng ngi ang online mi , ko bao gm trong danh sch c
    this.hubConnection.on("UserIsOnline" , username => {   
      // this.toast.info(username + "had connected");
      // thay bng cp nht Username OnlineisOnlien trong PresentTrack
      this.onlineUser$.pipe(take(1)).subscribe(usernames => { // messages l 1 array user ang online
        // cp nht li ds ngi ang online khi h c connection 
        this.userOnlineSource.next([...usernames , username]);
      }) 
    });
    this.hubConnection.on("UserIsOffline" , username => {
     //  this.toast.warning(username + "had disconnected");
     this.onlineUser$.pipe(take(1)).subscribe(usernames => { // messages l 1 array user ang Offline
      // cp nht li ds ngi ang online khi h mt connection 
      // danh sch online s cn li nhng usernames(online) m ko nm trong bng usernae (offline)
      this.userOnlineSource.next([...usernames.find(x => x != username )]);
    }) 

    })
    // n s t ng kt ni vi Presencehub.CS v gi phng thc GetOnlineUsers
    this.hubConnection.on("GetOnlineUsers" , (username: string[]) => {
        this.userOnlineSource.next(username);
    } )

    // hub.clients.clients s lin kt vi hm on  y
    this.hubConnection.on("NewMessageRecived" , ({username , knownas}) => {
      this.toast.info(username + "had send a messages for you" )
                .onTap // trigger khi click vo Toast
                .pipe(take(1))
                .subscribe(() => this.router.navigateByUrl("/members/" + username + "?tab=3"));
    });
    
  }
  stopHubConnection() {
    this.hubConnection.stop().catch(error => console.log(error));
  }
  
}
