import { Component, OnInit, TemplateRef } from '@angular/core';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';

@Component({
  selector: 'app-confirm-dialog',
  templateUrl: './confirm-dialog.component.html',
  styleUrls: ['./confirm-dialog.component.css']
})
export class ConfirmDialogComponent implements OnInit {

  title: string; 
  message: string;
  btnOkText: string
  btnCanceText: string;
  result: boolean;
  // bsModelRef s gip chng ta truy xut v truyn d liu title , message....(khi to trong service) 
  //v ly Result (khi to trong component) qua li gia Sevice v Component
  constructor(public bsModelRef: BsModalRef) { } 

  ngOnInit(): void {
  }
  confirm() {
    this.result = true ;
    this.bsModelRef.hide();
  }
  decline() {
    this.result = false;
    this.bsModelRef.hide();
  }

  // openModal(template: TemplateRef<any>) {
  //   this.bsModelRef = this.bsModalService.show(template, {class: 'modal-sm'})
  // }

}
